module.exports = "<h1 id=\"vcl-icon\">vcl-icon</h1>\n<p>Icon which can be based on glyphs from icon fonts, inline svg and bitmaps.</p>\n<p>The <code>label</code> is never displayed, it is only for accessibility with screen<br>readers.<br>The <code>hidden</code> attribute is only reflected in the <code>aria-hidden</code> property which<br>allows to hide the icon to screen readers, if it is only of presentational character.<br>See <a href=\"http://www.filamentgroup.com/lab/bulletproof_icon_fonts.html\">http://www.filamentgroup.com/lab/bulletproof_icon_fonts.html</a> for details.</p>\n<h2 id=\"usage-\">Usage:</h2>\n<p>As a component</p>\n<pre class=\"hljs\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">vcl-icon</span> <span class=\"hljs-attr\">icon</span>=<span class=\"hljs-string\">&quot;...&quot;</span> <span class=\"hljs-attr\">label</span>=<span class=\"hljs-string\">&quot;chevron right&quot;</span> <span class=\"hljs-attr\">hidden</span>=<span class=\"hljs-string\">&quot;false&quot;</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">vcl-icon</span>&gt;</span>\n</pre>\n<p>or via an attribute</p>\n<pre class=\"hljs\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">span</span> <span class=\"hljs-attr\">vcl-icon</span> <span class=\"hljs-attr\">icon</span>=<span class=\"hljs-string\">&quot;...&quot;</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">span</span>&gt;</span>\nWith image resource\n```html\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">vcl-icon</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">&quot;...&quot;</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">vcl-icon</span>&gt;</span>\n</pre>\n<p>With SVG</p>\n<pre class=\"hljs\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">vcl-icon</span> <span class=\"hljs-attr\">svguse</span>=<span class=\"hljs-string\">&quot;...&quot;</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">vcl-icon</span>&gt;</span>\n</pre>\n<h3 id=\"api\">API</h3>\n<h4 id=\"properties-\">Properties:</h4>\n<table>\n<thead>\n<tr>\n<th>Name</th>\n<th>Type</th>\n<th>Default</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>icon</code></td>\n<td>string</td>\n<td></td>\n<td>Icon generator lookup via icon class provider</td>\n</tr>\n<tr>\n<td><code>src</code></td>\n<td>string</td>\n<td></td>\n<td>URL of a graphics resource</td>\n</tr>\n<tr>\n<td><code>svguse</code></td>\n<td>string</td>\n<td></td>\n<td>Generates an SVG <code>use</code> tag referencing the value</td>\n</tr>\n<tr>\n<td><code>iconClass</code></td>\n<td>string</td>\n<td></td>\n<td>Additional class</td>\n</tr>\n<tr>\n<td><code>label</code> <em>(1)</em></td>\n<td>string</td>\n<td></td>\n<td><code>aria-label</code> </td>\n</tr>\n<tr>\n<td><code>ariaRole</code></td>\n<td>string</td>\n<td></td>\n<td><code>aria-role</code></td>\n</tr>\n</tbody>\n</table>\n<p><em>(1) Supports l10n</em></p>\n"

